body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  margin: 0;
  background-color: yellow; }

.app__title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-transform: uppercase; }

.app__main {
  margin: 0 auto; }

.app__field {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }

.app__field-search {
  height: 40px;
  width: 500px;
  border: none;
  outline: none;
  text-align: center;
  box-shadow: 0px 0px 10px 0px rgba(128, 128, 128, 0.719); }

.pokemons__list {
  margin: 25px 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center; }

.pokemons__list-pokemon {
  list-style: none;
  width: 300px;
  padding: 0;
  margin: 0 0 10px;
  border: 1px solid lightgray;
  box-shadow: 0px 0px 10px 0px rgba(189, 189, 189, 0.836); }

.image__container {
  height: 200px;
  width: 300px;
  background-color: #e6e6e6;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box; }

.pokemon__image {
  height: 100px;
  width: 100px; }

.info__container {
  background-color: white; }

.pokemon__name {
  text-transform: capitalize;
  margin: 30px 0 5px 15px; }

.pokemon__types {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  text-align: center; }

.pokemon__types-item {
  height: 30px;
  width: 80px;
  text-transform: uppercase;
  border: 1px solid rgba(63, 61, 61, 0.904);
  border-radius: 10px;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 0 30px 15px; }

